@import url(https://fonts.googleapis.com/css2?family=Do+Hyeon&family=Montserrat&family=Noto+Sans+KR&display=swap);
body {
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}
.cities {
  display: grid;

  grid-template: repeat(4, 1fr) / repeat(2, 1fr);
  justify-content: center;
  align-items: center;
}
.city {
  height: 20vh;
  text-decoration: none;
  font-family: "Montserrat", sans-serif;
  color: #000;
  opacity: 0.5;
  font-size: 4rem;
  padding: 4vh 5vw;
  /* border: 1px solid cadetblue;
  border-radius: 15px; */
  margin: 4vh 0;
  text-align: center;
}

.location,
.temp,
.temp_range,
.humidity,
.recent_weather {
  text-align: center;
  font-family: "Do Hyeon", sans-serif;
  font-size: 2rem;
}
#icon {
  display: block;
  margin: 0px auto;
  width: 20%;
}
.info {
  opacity: 0.5;
}
.recent_weather {
  margin-top: 50px;
}
.humidity {
  margin-top: 50px;
}
.temp {
  display: block;
  margin-top: 50px;
}
.location {
  font-family: "Montserrat", sans-serif;
  margin: 5vh 0;
  font-size: 3.5rem;
}

